.getstarted {
  background-color: #fbf8f5;

  .getstarted__wrapper {
    display: flex;

    .started_img {
      animation: img_anim 1s ease-out;
      opacity: 0;
      animation-fill-mode: forwards;
      width: 100%;

      &.visible {
        opacity: 1;
      }

      @keyframes img_anim {
        0% {
          opacity: 0;
        }
        100% {
          opacity: 1;
        }
      }
    }

    .started_text {
      margin-left: 100px;
      padding-top: 90px;

      h1 {
        font-size: 64px;
        line-height: 76px;

        animation-name: slideIn;
        animation-duration: 1s;
        animation-timing-function: ease-out;
        animation-fill-mode: forwards;

        @keyframes slideIn {
          0% {
            transform: translateX(-100%);
            opacity: 0;
          }
          50% {
            opacity: 0.5;
          }
          100% {
            transform: translateX(0%);
            opacity: 1;
          }
        }
      }

      p {
        font-size: 18px;
        line-height: 28px;
        margin-right: 130px;

        animation-name: slideIn2;
        animation-duration: 1s;
        animation-timing-function: ease-out;
        animation-fill-mode: forwards;

        @keyframes slideIn2 {
          0% {
            transform: translateX(-100%);
            opacity: 0;
          }
          50% {
            transform: translateX(10%);
            opacity: 0;
          }
          100% {
            transform: translateX(0%);
            opacity: 1;
          }
        }
      }

      .started_btn {
        padding: 46px 0;

        animation: img_anim 2s ease-out;
        opacity: 0;
        animation-fill-mode: forwards;
        a {
          padding: 16px 32px;
          background-color: #e9672b;
          border-radius: 30px;

          &:hover {
            color: white;
            transition: color 0.3s ease-in-out;
          }
        }
      }
    }
  }
}

