@media (max-width: 400px) {
  .getstarted {
        background-image: url("../assets/images/getstarted.png");
        background-repeat: no-repeat;
        background-size: auto;
        background-position: left center;
    .getstarted__wrapper {
      .started_text {
        margin-left: 0;
        padding-top: 40px;
        text-align: center;
        margin: 0 10px;

        p {
          margin-right: 0;
          padding-bottom: 50px;
        }

        .started_btn {
          padding: 0 0;
        }
      }
      .started_img {
        display: none;
      }
    }
  }
}
